// 引入自定义评分组件
import trailerStars from '../../components/trailerStars.vue'
export default {
	data() {
		return {
			carouselList: [],
			hotSuperHeroList: [],
			hotTrailerList: [],
			guessULikeList: [],
			
			animationData: {},
			animationDataArr: [
				// {},{},{},{},{}
			]
			
			
		}
	},
	// 页面卸载时的生命周期函数
	onUnload(){
		// 页面卸载的时候清除动画数据
		this.animationData = {}
		this.animationDataArr = []
	},
	onLoad() {

		// 在页面创建的时候，创建一个临时动画对象
		this.animation = uni.createAnimation()

		
		// 查询轮播图
		uni.request({
			url: this.baseUrl + '/index/carousel/list?type=superhero',
			method: 'post',
			header:{
				'content-type':'application/x-www-form-urlencoded'
				},
			data: {
				qq: '1335436466'
			},
			success: res => {
				if (res.data.status === 200){
					this.carouselList = res.data.data
				}
			}
		});
		
		// 查询热门英超
		uni.request({
			url: this.baseUrl + '/index/movie/hot',
			method: 'post',
			header:{
				'content-type':'application/x-www-form-urlencoded'
				},
			data: {
				qq: '1335436466',
				type: 'superhero'
			},
			success: res => {
				if (res.data.status === 200){
					this.hotSuperHeroList = res.data.data
				}
			}
		});
		
		// 查询热门英超预告片
		uni.request({
			url: this.baseUrl + '/index/movie/hot',
			method: 'post',
			header:{
				'content-type':'application/x-www-form-urlencoded'
				},
			data: {
				qq: '1335436466',
				type: 'trailer'
			},
			success: res => {
				if (res.data.status === 200){
					this.hotTrailerList = res.data.data
				}
			}
		});
		
		// 查询猜你喜欢列表
		uni.request({
			url: this.baseUrl + '/index/guessULike',
			method: 'post',
			header:{
				'content-type':'application/x-www-form-urlencoded'
				},
			data: {
				qq: '1335436466'
			},
			success: res => {
				if (res.data.status === 200){
					this.guessULikeList = res.data.data
				}
			}
		});
	},
	methods: {
		// 实现点赞动画效果
		praiseMe(e){

			let guessIndex = e.currentTarget.dataset.gindex
			// 构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-60).opacity(1).step({duration: 400});
			
			// 导出动画数据到view组件，实现组件的动画效果
			// this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataArr[guessIndex] = this.animationData.export();
			
			// 还原动画
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({duration: 0});
				// this.animationData = this.animation.export();
				this.animationData = this.animation;
				this.animationDataArr[guessIndex] = this.animationData.export();
			}.bind(this), 500);

		}
	},
	components: {
		// 注册自定义组件
		trailerStars
	}
}
